  padding: 14px 0;
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 15px;
  font-weight: 400;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  text-decoration: none;
  text-align: center;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.contact-btn.groom {
  background-color: #8ea4c1;
  box-shadow: 0 4px 15px rgba(142, 164, 193, 0.2);
}

.contact-btn.bride {
  background-color: var(--accent-color);
  box-shadow: 0 4px 15px rgba(232, 167, 152, 0.2);
}

.contact-btn.kakao {
  background-color: #fee500;
  color: #3c1e1e;
  box-shadow: 0 4px 15px rgba(254, 229, 0, 0.2);
}

.contact-btn:hover {
  transform: translateY(-2px);
}

.contact-btn::after {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    transparent,
    rgba(255, 255, 255, 0.2),
    transparent
  );
  transform: rotate(30deg);
  transition: all 0.8s;
}

.contact-btn:hover::after {
  left: 100%;
}

footer {
  text-align: center;
  padding: 40px 0 30px;
  font-size: 13px;
  color: var(--light-text);
  background-color: #fdf5f2;
  position: relative;
}

footer::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 8px;
  background: linear-gradient(
    to right,
    var(--secondary-color),
    var(--primary-color),
    var(--secondary-color)
  );
  opacity: 0.5;
}

.heart-icon {
  display: inline-block;
  color: var(--primary-color);
  margin: 0 5px;
  animation: heartbeat 1.5s infinite;
}

@keyframes heartbeat {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}

/* 날짜 카운트다운 */
