/* 축하메시지 섹션 */
.congratulation-container {
  background-color: white;
  border-radius: 15px;
  padding: 20px;
  margin: 0 20px 30px;
  box-shadow: 0 5px 25px rgba(232, 167, 152, 0.1);
}

.congratulation-list {
  max-height: 300px;
  overflow-y: auto;
  margin-top: 15px;
  padding: 5px;
}

.congratulation-item {
  border-bottom: 1px solid var(--border-color);
  padding: 15px 10px;
  margin-bottom: 10px;
}

.congratulation-content {
  margin: 8px 0;
  line-height: 1.5;
  color: var(--text-color);
}

.author-date {
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  color: var(--light-text);
}

.congratulation-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
}

input,
textarea {
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 12px 15px;
  font-size: 14px;
