  box-shadow: 0 4px 15px rgba(232, 167, 152, 0.25);
}

button:hover,
.button:hover {
  background-color: var(--accent-color);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(231, 144, 128, 0.3);
}

.kakao-share-btn {
  background-color: #fee500;
  color: #3a1d1d;
  border-radius: 25px;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px auto;
  width: fit-content;
  box-shadow: 0 4px 12px rgba(254, 229, 0, 0.2);
}

.kakao-share-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 18px rgba(254, 229, 0, 0.3);
}

.kakao-share-btn img {
  width: 18px;
  height: 18px;
  margin-right: 8px;
}

/* 데코레이션 요소 추가 */
.floral-deco {
  position: absolute;
  z-index: 1;
  opacity: 0.7;
}

